<template>
  <div class="container">
    <div id="index-blogs">
      <div id="header">
        <h1>All Posts</h1>
      </div>

      <div>
        <div id="search">
          <input type="text" placeholder="search blogs" v-model="searchQuery">
        </div>
        <div id="blog-list">
          <div v-for="(blog, i) in filteredBlogs" v-bind:key="i" class="blog">
            <h2>
              <router-link v-rainbow v-bind:to="blog.url">{{blog.title }}</router-link>
            </h2>
            <p v-beautify>{{blog.content | snippet}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { postMixin } from "../mixins";

export default {
  data() {
    return {
      searchQuery: "",
      blogs: []
    };
  },

  computed: {
    filteredBlogs() {
      return this.blogs
        .reverse()
        .filter(blog => blog.title.match(this.searchQuery));
    }
  },

  filters: {
    upcase: value => value.toUpperCase(),
    snippet: value => value.slice(0, 100) + "..."
  },

  created() {
    this.$http
      .get("https://vue-blog-19.firebaseio.com/posts.json")
      .then(data => {
        Object.keys(data.body).forEach(key => {
          const post = Object.assign({ url: `post/${key}` }, data.body[key]);
          this.blogs.push(post);
        });
      });
  },

  mixins: [postMixin]
};
</script>

<style scoped>
#search input {
  box-sizing: border-box;
  width: 100%;
  font-size: 14px;
  height: 40px;
  padding: 0 10px;
}

#search,
.blog {
  margin-bottom: 15px;
}

.blog {
  background: #eee;
  padding: 20px;
  border-radius: 5px;
}
</style>
