<template>
  <div class="container">
    <div id="index-blogs">
      <div id="header">
        <h1>{{blog.title}}</h1>
      </div>

      <div id="blog">
        <p v-beautify>{{blog.content}}</p>
      </div>
    </div>
  </div>
</template>

<script>
import { postMixin } from "../mixins";

export default {
  data() {
    return {
      id: `${this.$route.params.id}.json`,
      blog: {}
    };
  },
  created() {
    this.$http
      .get("https://vue-blog-19.firebaseio.com/posts/" + this.id)
      .then(data => (this.blog = data.body));
  },

  mixins: [postMixin]
};
</script>

