<template>
  <div class="container">
    <div v-if="!submitted" id="new-blog-form">
      <div id="header">
        <h1>New Post</h1>
      </div>

      <div id="body">
        <div class="item">
          <label for>Title:</label>
          <input type="text" v-model.lazy="blog.title">
        </div>

        <div class="item">
          <label for>Content:</label>
          <textarea cols="30" rows="8" v-model.lazy="blog.content"></textarea>
        </div>

        <div class="group-items">
          <label>Ninjas</label>
          <input type="checkbox" value="ninjas" v-model="blog.categories">
          
          <label>Wizards</label>
          <input type="checkbox" value="wizard" v-model="blog.categories">
          
          <label>Mario</label>
          <input type="checkbox" value="mario" v-model="blog.categories">
          
          <label>Cheese</label>
          <input type="checkbox" value="cheese" v-model="blog.categories">
        </div>

        <div class="item">
          <label>Author:</label>
          <select v-model="blog.author">
            <option v-for="(author, i) in authors" v-bind:key="i">{{author}}</option>
          </select>
        </div>
      </div>

      <div id="footer">
        <button v-on:click.prevent="post">Post</button>
      </div>
    </div>

    <div id="success-message" v-if="submitted">
      <p>Many Thanks, Your Post is successfully published</p>
    </div>

    <div id="new-blog-preview">
      <div id="header">
        <h2>Preview</h2>
      </div>

      <div id="body">
        <div>
          <p>Blog Title: {{blog.title}}</p>
        </div>

        <div>
          <label>Blog Content:</label>
          <p v-beautify>{{blog.content}}</p>
        </div>

        <div>
          <label>Blog Categories:</label>
          <ul>
            <li v-for="(category, i) in blog.categories" v-bind:key="i">{{category}}</li>
          </ul>
        </div>

        <div>
          <p>Author: {{blog.author}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { postMixin } from "../mixins";

export default {
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: ""
      },

      authors: [
        "The Net Ninja",
        "The Vue Vindicator",
        "The Angular Maestro",
        "The HTML Wizard"
      ],

      submitted: false
    };
  },

  methods: {
    post() {
      this.$http
        .post("https://vue-blog-19.firebaseio.com/posts.json", this.blog)
        .then(data => (this.submitted = true));
    }
  },

  mixins: [postMixin]
};
</script>

