export default {
    directives: {

        beautify: {
            bind(el, binding, vnode) {
                el.style.textAlign = "justify";
                el.style.lineHeight = '40px';
            }
        },

        rainbow: {
            bind(el, binding, vnode) {
                el.style.color = `#${Math.random()
                    .toString()
                    .slice(2, 8)}`;
            }
        },
    }
}